"use client"

import { useState, useEffect, useMemo } from "react"
import { useSearchParams } from "next/navigation"
import { ChevronLeft, ChevronRight, Clock, MapPin, User, Hourglass } from "lucide-react"
import { lichDayData, giangVienData } from "@/lib/data"

// Hàm chuyển đổi thứ trong tuần
const getDayOfWeek = (date) => {
  const days = ["Chủ nhật", "Thứ 2", "Thứ 3", "Thứ 4", "Thứ 5", "Thứ 6", "Thứ 7"]
  return days[date.getDay()]
}

// Hàm lấy ngày đầu tuần (Thứ 2)
const getStartOfWeek = (date) => {
  const d = new Date(date)
  const day = d.getDay()
  const diff = d.getDate() - day + (day === 0 ? -6 : 1) // Adjust when day is Sunday
  return new Date(d.setDate(diff))
}

// Hàm format ngày
const formatDate = (date) => {
  const day = String(date.getDate()).padStart(2, "0")
  const month = String(date.getMonth() + 1).padStart(2, "0")
  const year = date.getFullYear()
  return `${day}/${month}/${year}`
}

export default function LichDayPage() {
  const searchParams = useSearchParams()
  const giangVienIdParam = searchParams?.get("giangVienId")

  const [currentWeek, setCurrentWeek] = useState(() => {
    const today = new Date()
    return getStartOfWeek(today)
  })

  const [selectedGiangVienId, setSelectedGiangVienId] = useState(giangVienIdParam || null)

  // Cập nhật selectedGiangVienId khi searchParams thay đổi
  useEffect(() => {
    if (giangVienIdParam) {
      setSelectedGiangVienId(giangVienIdParam)
    }
  }, [giangVienIdParam])

  // Tính toán tuần hiện tại
  const weekStart = useMemo(() => {
    return new Date(currentWeek)
  }, [currentWeek])

  const weekEnd = useMemo(() => {
    const end = new Date(weekStart)
    end.setDate(end.getDate() + 6)
    return end
  }, [weekStart])

  // Lọc lịch dạy theo tuần và giảng viên
  const filteredLichDay = useMemo(() => {
    return lichDayData.filter((lich) => {
      // Lọc theo giảng viên nếu có chọn
      if (selectedGiangVienId && lich.giangVienId !== selectedGiangVienId) {
        return false
      }

      // Lọc theo tuần
      const lichDate = new Date(lich.ngay)
      return lichDate >= weekStart && lichDate <= weekEnd
    })
  }, [weekStart, weekEnd, selectedGiangVienId])

  // Chuyển tuần trước
  const goToPreviousWeek = () => {
    const newWeek = new Date(currentWeek)
    newWeek.setDate(newWeek.getDate() - 7)
    setCurrentWeek(newWeek)
  }

  // Chuyển tuần sau
  const goToNextWeek = () => {
    const newWeek = new Date(currentWeek)
    newWeek.setDate(newWeek.getDate() + 7)
    setCurrentWeek(newWeek)
  }

  // Quay về tuần này
  const goToCurrentWeek = () => {
    const today = new Date()
    setCurrentWeek(getStartOfWeek(today))
  }

  // Kiểm tra xem có phải tuần hiện tại không
  const isCurrentWeek = useMemo(() => {
    const today = new Date()
    const currentWeekStart = getStartOfWeek(today)
    return weekStart.getTime() === currentWeekStart.getTime()
  }, [weekStart])

  return (
    <main className="flex-1 p-6">
      <h1 className="text-[#4A90D9] text-2xl font-bold mb-6">Lịch dạy</h1>

      {/* Điều hướng tuần */}
      <div className="bg-white rounded-xl p-4 shadow-sm mb-6">
        <div className="flex items-center justify-between">
          <button
            onClick={goToPreviousWeek}
            className="p-2 rounded-lg hover:bg-gray-100 text-gray-600 hover:text-[#4A90D9] transition-colors"
          >
            <ChevronLeft className="w-5 h-5" />
          </button>

          <div className="flex items-center gap-4">
            <span className="text-gray-700 font-medium">
              {formatDate(weekStart)} - {formatDate(weekEnd)}
            </span>
            {!isCurrentWeek && (
              <button
                onClick={goToCurrentWeek}
                className="px-4 py-2 bg-[#4A90D9] text-white rounded-lg text-sm font-medium hover:bg-[#3a7bc8] transition-colors"
              >
                Tuần này
              </button>
            )}
          </div>

          <button
            onClick={goToNextWeek}
            className="p-2 rounded-lg hover:bg-gray-100 text-gray-600 hover:text-[#4A90D9] transition-colors"
          >
            <ChevronRight className="w-5 h-5" />
          </button>
        </div>
      </div>

      {/* Lọc theo giảng viên (nếu không có từ URL) */}
      {!giangVienIdParam && (
        <div className="mb-6">
          <select
            value={selectedGiangVienId || ""}
            onChange={(e) => setSelectedGiangVienId(e.target.value || null)}
            className="px-4 py-2.5 border border-gray-200 rounded-lg text-sm bg-white focus:outline-none focus:ring-2 focus:ring-[#4A90D9]"
          >
            <option value="">Tất cả giảng viên</option>
            {giangVienData
              .filter((gv) => gv.trangThai === "Đang hoạt động")
              .map((gv) => (
                <option key={gv.id} value={gv.id}>
                  {gv.hoTen}
                </option>
              ))}
          </select>
        </div>
      )}

      {/* Danh sách lịch dạy */}
      {filteredLichDay.length === 0 ? (
        <div className="bg-white rounded-xl p-12 shadow-sm text-center">
          <p className="text-gray-500">Không có lịch dạy nào trong tuần này.</p>
        </div>
      ) : (
        <div className="space-y-4">
          {filteredLichDay.map((lich) => {
            const lichDate = new Date(lich.ngay)
            const dayOfWeek = getDayOfWeek(lichDate)

            return (
              <div key={lich.id} className="bg-white rounded-xl p-6 shadow-sm border border-gray-100">
                <div className="flex items-start justify-between mb-4">
                  <div>
                    <h3 className="text-xl font-bold text-gray-800 mb-1">{lich.monHoc}</h3>
                    <p className="text-sm text-gray-600">{lich.lop}</p>
                  </div>
                  <button className="px-4 py-2 bg-gray-100 text-gray-700 rounded-lg text-sm font-medium hover:bg-gray-200 transition-colors">
                    Lịch dạy
                  </button>
                </div>

                <div className="grid grid-cols-2 gap-4">
                  <div className="flex items-center gap-3">
                    <Clock className="w-5 h-5 text-gray-400" />
                    <span className="text-sm text-gray-700">
                      {dayOfWeek}, {lich.thoiGian}
                    </span>
                  </div>
                  <div className="flex items-center gap-3">
                    <User className="w-5 h-5 text-gray-400" />
                    <span className="text-sm text-gray-700">GV.{lich.giangVien}</span>
                  </div>
                  <div className="flex items-center gap-3">
                    <MapPin className="w-5 h-5 text-gray-400" />
                    <span className="text-sm text-gray-700">{lich.phongHoc}</span>
                  </div>
                  <div className="flex items-center gap-3">
                    <Hourglass className="w-5 h-5 text-gray-400" />
                    <span className="text-sm text-gray-700">Thời lượng: {lich.thoiLuong} giờ</span>
                  </div>
                </div>
              </div>
            )
          })}
        </div>
      )}
    </main>
  )
}

